@import '../../styles/main.scss';

.app {
  height: 100vh;

  .container {
    display: flex;
    flex-flow: column;
    min-height: 100%;

    .main {
      background-color: var(--color-yellow-s);
      display: grid;
      grid-template-columns: 34rem 1fr;
      flex: 1;
    }
  }
}
